<template>
    <div class="ui-kit-switch">
        <div class="switch-item"
             v-for="(item, index) in options"
             :key="index"
             :ref="item.value"
             :class="{active: item.value === value}"
             @click="onchange(item)"
        >
            <b>{{item.name}}</b>
        </div>
        <div class="switch-check" :style="{width: `${cpd_position.width}px`, left: `${cpd_position.left}px`}"></div>
    </div>
</template>

<script>
export default {
    name: 'UiKitSwitch',
    model: {
      prop: 'value',
      event: 'change'
    },
    props: {
        value: {
            type: String | Number,
            default: ''
        },
        options: {
            type: Array,
            default: () => []
        }
    },
    data() {
      return {
          positions: {}
      }
    },
    computed: {
      cpd_position() {
          let position = this.positions[this.value]
          console.log(this.positions, this.value)
          if (!position) {
              position = {
                  left: 0,
                  width: 0
              }
          }
          return position
      }
    },
    created() {
        if (!this.value && this.options.length > 0) {
            this.$emit('change', this.options[0].value)
        }
    },
    mounted() {
        this.$nextTick(() => {
            const refValues = Object.values(this.$refs)
            const refKeys = Object.keys(this.$refs)
            const positions = {}
            refValues.reduce((left, item, index) => {
                const width = item[0].offsetWidth
                positions[refKeys[index]] = {
                    left,
                    width
                }
                return left + width
            }, 3)
            console.log(positions, 1111111)
            this.positions = positions
        })
    },
    methods: {
        onchange(item) {
            this.$emit('change', item.value)
        }
    }
}
</script>

<style lang="less">
.ui-kit-switch {
    display: flex;
    position: relative;
    height: 28px;
    padding: 3px 4px 3px 3px;
    background-color: @boxColor;
    border-radius: 14px;
    .switch-item {
        padding: 0 12px;
        position: relative;
        display: flex;
        align-items: center;
        z-index: 1;
        font-size: 12px;
        cursor: pointer;
        &.active {
            color: #000;
        }
    }
    .switch-check {
        position: absolute;
        height: 22px;
        border-radius: 11px;
        top: 3px;
        background-color: @buttonColor;
        transition-property: left,width;
        transition-duration: 0.3s;
    }
}
</style>
